+font(avantgardegothiccbold, avantgardegothiccbold)

=avant
	font-family: 'avantgardegothiccbold', Arial, sans-serif
=osl
	font-family: 'Open Sans', Arial, sans-serif
	font-weight: 300
=os
	font-family: 'Open Sans', Arial, sans-serif
	font-weight: 400

$purpur: #eb6c96
$blue: #52abf0
$n: transparent
$black: #23252a


html, body
	height: 100%
body
	font-size: 16px
	line-height: 1.5

.container__big
	width: 90%
	max-width: 1792px
	margin: 0 auto
	+clr
.container
	width: 76.6%
	max-width: 1974px
	margin: 0 auto
	+clr


*
	outline: none
	+selection
		background-color: yellow
		color: $blue

a
	text-decoration: none

.out
	min-height: 100%
	height: auto
	padding-top: 0
